import React, { Component } from 'react';
import Button from "react-bootstrap/Button";


class TestItem extends Component {

    constructor(props){
        super(props);

        this.handleShow = this.handleShow.bind(this);

        this.state = {
            testString: this.props.testString,
            decoration:"none"
        }
    }

    handleShow() {
        this.setState({ decoration: "line-through" });
    }

    render(){
        return(
            <div>
                <p className="test-case" style={{"text-decoration":this.state.decoration}}>{this.state.testString}</p>
                <Button onClick={this.handleShow} className="done">Mark as Done</Button>
            </div>
        );
    }

}

export default TestItem;


