import React, { Component, } from 'react';
import './graph.css';
import Graph from 'react-graph-vis';
import Button from "react-bootstrap/Button";
import {Modal} from "react-bootstrap/index";

class MyGraph extends Component {

    constructor(props){
        super(props);

        this.handleShow = this.handleShow.bind(this);
        this.handleClose = this.handleClose.bind(this);

        this.state = {
            show: false
        };
    }

    handleClose() {
        this.setState({ show: false });
    }

    handleShow() {
        this.setState({ show: true });
    }

    showInput = () => {
        if (this.state.inputState ===0){
            this.setState({inputState:1});
        } else if (this.state.inputState ===1){
            this.setState({inputState:0});
        }
    };

    getNodes(length) {
        // define the nodes array
        let nodes = [];
        for (let i=0; i<length; i++){
            nodes[i] = {id:i, label:"Node "+i, color:'#' + Math.random().toString(16).slice(2, 8).toUpperCase()}
        }
        return nodes;
    }

    render() {

        let theGraph = {
            nodes: this.getNodes(15),
           edges: [
               {from:0 , to: 1},
               {from:0 , to: 2},
               {from:0 , to: 3},
               {from:0 , to: 4},
               {from:0 , to: 5},
               {from:0 , to: 6},
               {from:2 , to: 7},
               {from:0 , to: 9},
               {from:6 , to: 10},
               {from:1 , to: 10},
               {from:3 , to: 9},
               {from:3 , to: 11},
               {from:4 , to: 5},
               {from:5 , to: 8},
               {from:5 , to: 6},
               {from:11 , to: 13},
               {from:9 , to: 14},
               {from:14 , to: 8},
               {from:7 , to: 13},
               {from:13 , to: 12},
            ]

        };

        let options = {
            layout: {
                hierarchical: false
            },
            edges: {
                color: "#ffffff"
            },
            height: "600px",
        };

        let events = {
            select: function(event) {
                let { nodes, edges } = event;
            }
        };
        return (
            <div className="graph-container">
                <p className="graph-title">Sample Network</p>
                <Graph graph={theGraph} options={options} events={events} />
                <p className="graph">The graph above represents a sample social network. I am Node 0, lets find how many people I need to invite at minimum to meet Node 8.</p>
                <Button
                    className="edges-button"
                    onClick={this.handleShow}
                >
                    Click for the edges!
                </Button>

                <Modal show={this.state.show} onHide={this.handleClose}>
                    <Modal.Header closeButton>
                        <Modal.Title>Sample network edges</Modal.Title>
                    </Modal.Header>
                    <Modal.Body>
                        <div id="example-collapse-text">
                        <div className="graph-input">
                            <p className="graph-edges">0 1</p>
                            <p className="graph-edges">0 2</p>
                            <p className="graph-edges">0 3</p>
                            <p className="graph-edges">0 4</p>
                            <p className="graph-edges">0 5</p>
                            <p className="graph-edges">0 6</p>
                            <p className="graph-edges">2 7</p>
                            <p className="graph-edges">0 9</p>
                            <p className="graph-edges">6 10</p>
                            <p className="graph-edges">1 10</p>
                            <p className="graph-edges">3 9</p>
                            <p className="graph-edges">3 11</p>
                            <p className="graph-edges">4 5</p>
                            <p className="graph-edges">5 8</p>
                            <p className="graph-edges">5 6</p>
                            <p className="graph-edges">11 13</p>
                            <p className="graph-edges">9 14</p>
                            <p className="graph-edges">14 8</p>
                            <p className="graph-edges">7 13</p>
                            <p className="graph-edges">13 12</p>
                        </div>
                    </div>
                    </Modal.Body>
                    <Modal.Footer>
                        <Button variant="secondary" onClick={this.handleClose}>
                            Got it!
                        </Button>
                    </Modal.Footer>
                </Modal>

            </div>
        );

    }
}

export default MyGraph;