import React, { Component } from 'react';
import './question.css';
import {Table} from "react-bootstrap/index";

class Question extends Component {
    render() {
        return (
            <div>
                <h1 className="quest-title">How much does it cost to meet Emma Stone?</h1>
                <p className="quest">I am throwing a party to meet Emma Stone. I can invite as many friends as I want to. For each person I invite, I am paying 20 TL.</p>
                <p className="quest">I am giving you a list of my social network. My list of friends are in the input data below. Each line represents the friends of my friends. </p>
                <Table className="table" striped bordered hover>
                    <thead>
                    <tr>
                        <th>My Id</th>
                        <th>Emma Stone's Id</th>
                    </tr>
                    </thead>
                    <tbody>
                    <tr>
                        <td>0</td>
                        <td>4038</td>

                    </tr>
                    </tbody>
                </Table>

                <p className="quest-rules">Here are some rules:</p>
                <p className="quest">1. Each person who is directly friends with me and are invited to the party, is coming to the party.</p>
                <p className="quest">2. Each invited person whose 1st degree friend is coming to the party (in other words, is invited), is also coming to the party.</p>

                <p className="quest">How much will it cost in in minimum to meet Emma Stone?</p>

                <p className="quest">You can download the input data "facebook_combined.txt.gz" from <a href="http://snap.stanford.edu/data/ego-Facebook.html" target="_blank" rel="noopener noreferrer">here</a>.</p>
                <p className="quest">Sample input format:</p>
                <div className="input-format">
                <p className="quest">0 # source</p>
                <p className="quest">4038 # target</p>
                <p className="quest">0 1  # 0 is friends with 1</p>
                <p className="quest">0 2</p>
                <p className="quest">0 3</p>
                <p className="quest">--rest of the edges--</p>
                </div>

            </div>
        );

    }
}

export default Question;